import React, { useCallback, useState, useMemo } from 'react';
import Debug from 'debug';
import { CalendarCardPlugin } from '@ali/4ever-bamboo';
import { Controller, RenderNodeProps, Block } from '@ali/4ever-cangjie';
import Container from '../components/Container';
import FlashCalendarContainer from '../shanhui/FlashCalendarContainer';
import {
  CalendarCardDataType,
  CalendarCardMetadataType,
  CalendarCardPluginConfig,
  CommonMouseEvent,
  JumpToCalendarEventType,
  CalendarInjectionType,
  CalendarType,
} from '../types';
import { setCardInjection, setCardData } from '@ali/4ever-factory';
import { calculatedSize, getCalculatedReceivers } from '../utils/calculatedSize';
import { calculatedCalendarSize, getCalculatedCalendarReceivers } from '../shanhui/calculatedCalendarSize';
import useRefreshResult from '../hooks/useRefreshResult';
import useRenderCalendarBtn from '../hooks/useRenderCalendarBtn';
import isEqualValue from '../utils/isEqualValue';
import useInit from '../hooks/useInit';

const debug = Debug('EDITOR:BI:CALENDAR:RENDER');

const { CALENDAR_CARD_MIN_HEIGHT } = CalendarCardPlugin;

interface IProps {
  nodeProps: RenderNodeProps<Block>;
  controller: Controller;
  calendarConfig: CalendarCardPluginConfig;
}

export const Calendar: React.FC<IProps> = ({
  nodeProps,
  controller,
  calendarConfig,
}) => {
  const {
    locale,
    jumpToCalendar,
    openProfile,
    jumpToAttachment,
    onRefreshSuccess,
    refresh,
    isMobile,
    getContainerWidthInPC = () => 0,
    handleCreateCalendar,
    onInitData,
    renderCreateCalendarBtn: CreateCalendarBtn,
    mode: calendarMode,
  } = calendarConfig;
  const { isFocused } = controller.value;
  const containerWidthInPC = useMemo(() => {
    return getContainerWidthInPC() - 4;
  }, [getContainerWidthInPC]);
  const { node, isSelected } = nodeProps;
  const nodeKey = node.key;
  const finalData = node.data as CalendarCardDataType;
  const finalHeight = isMobile ? 'auto' : (finalData.height || CALENDAR_CARD_MIN_HEIGHT);
  const { injectionMetadata, injectionHeight, isFirstLoaded = !!isMobile } = finalData as CalendarInjectionType;
  const { isInitialized, isPlaceholder, mode = calendarMode } = finalData.metadata;
  const finalMetadata = injectionMetadata || finalData.metadata;
  const { calendarId, redirectUrl } = finalMetadata;
  const [isLoading, setLoading] = useState<boolean>(false);
  const isShanhui = mode === CalendarType.SHANHUI;

  // 点击刷新按钮后的消息反馈
  const { refreshTipsNode, setRefreshResult } = useRefreshResult();

  const injectToData = useCallback((mergeData: CalendarInjectionType) => {
    const latestNode = controller.value.document.getNode(node.key) as Block;
    controller.run('onAction', setCardInjection(latestNode, { injectionMetadata, injectionHeight, isFirstLoaded, ...mergeData }));
  }, [controller, node.key, injectionMetadata, injectionHeight, isFirstLoaded]);

  // 设置日程卡片 data
  const setCalendarCardData = useCallback(async (newMetadata: CalendarCardMetadataType) => {
    const calculateHeight = isShanhui ? calculatedCalendarSize : calculatedSize;
    const height = await calculateHeight(newMetadata, containerWidthInPC);
    const latestNode = controller.value.document.getNode(node.key) as Block;
    controller.run('onAction', setCardData(latestNode, {
      metadata: { ...newMetadata, mode, isInitialized: true },
      height,
      withoutSaving: true,
    }));
  }, [containerWidthInPC, controller, node.key, mode, isShanhui]);

  // 创建右上角日程按钮
  const [createCalendarBtn] = useRenderCalendarBtn({
    isPlaceholder,
    calendarId,
    containerWidthInPC,
    finalMetadata,
    injectToData,
    setLoading,
    setCalendarCardData,
    CalendarBtn: CreateCalendarBtn,
    key: nodeKey,
  });

  // 点击创建日程按钮
  const processCreateCalendar = useCallback(() => {
    if (handleCreateCalendar) handleCreateCalendar(nodeKey);
  }, [handleCreateCalendar, nodeKey]);

  // 调整到日程容器
  const goToCalendar = useCallback((eventType?: JumpToCalendarEventType) => {
    if (!jumpToCalendar || !redirectUrl) return;
    jumpToCalendar(redirectUrl, eventType);
  }, [jumpToCalendar, redirectUrl]);

  // 将日程数据持久化存储到卡片中
  const setRefreshDataToNode = useCallback(() => {
    if (injectionMetadata) {
      const latestNode = controller.value.document.getNode(node.key) as Block;
      controller.run('onAction', setCardData(latestNode, {
        metadata: { ...injectionMetadata, mode, isInitialized: true },
        height: injectionHeight,
        withoutSaving: true,
      }));
      injectToData({
        injectionMetadata: undefined,
        injectionHeight: undefined,
        isFirstLoaded: true,
      });
      if (onRefreshSuccess) onRefreshSuccess();
    }
  }, [injectionMetadata, controller, node.key, mode, injectionHeight, injectToData, onRefreshSuccess]);

  const fireRefresh: CommonMouseEvent = useCallback((event) => {
    event.preventDefault();
    event.stopPropagation();
    if (!refresh) return;
    if (injectionMetadata) {
      setRefreshDataToNode();
      return;
    }
    setLoading(true);
    refresh(calendarId, 'click').then(async (res) => {
      setLoading(false);
      if (res.status !== 'success') return;
      if (!containerWidthInPC) return;
      const getTopReceivers = isShanhui ? getCalculatedCalendarReceivers : getCalculatedReceivers;
      const topReceivers = await getTopReceivers(res.data!, containerWidthInPC, res.data!.totalCount!);
      const newMetadata = {
        calendarId,
        ...res.data,
        topReceivers,
      };
      if (!isEqualValue(newMetadata, finalMetadata)) {
        setCalendarCardData(newMetadata);
      }
      if (onRefreshSuccess) onRefreshSuccess();
    }).catch(() => {
      setLoading(false);
    });
  }, [
    refresh,
    injectionMetadata,
    calendarId,
    setRefreshDataToNode,
    containerWidthInPC,
    isShanhui,
    finalMetadata,
    onRefreshSuccess,
    setCalendarCardData,
  ]);

  const getLoadingState = useCallback(() => {
    debug('LOADINGSTATE', isFirstLoaded, isLoading);
    return !isFirstLoaded ? true : isLoading;
  }, [isFirstLoaded, isLoading]);

  useInit({
    isInitialized,
    isPlaceholder,
    containerWidthInPC,
    isLoading,
    isFirstLoaded,
    isMobile,
    calendarId,
    setLoading,
    refresh,
    controller,
    node,
    finalMetadata,
    finalData,
    injectToData,
    setRefreshResult,
    onInitData,
    mode,
  });


  if (isShanhui) {
    return (
      <FlashCalendarContainer
        refreshTipsNode={refreshTipsNode}
        height={finalHeight}
        isLoading={isLoading}
        isFirstLoaded={isFirstLoaded}
        isChangeData={!!injectionMetadata}
        metadata={{ ...finalMetadata, mode }}
        locale={locale}
        isSelected={isSelected && isFocused}
        width={containerWidthInPC}
        jumpToCalendar={goToCalendar}
        openProfile={openProfile}
        jumpToAttachment={jumpToAttachment}
        refresh={fireRefresh}
        isMobile={isMobile}
        handleCreateCalendar={processCreateCalendar}
        createCalendarBtn={createCalendarBtn}
      />
    );
  }

  return (
    <Container
      refreshTipsNode={refreshTipsNode}
      height={finalHeight}
      isLoading={getLoadingState()}
      isFirstLoaded={isFirstLoaded}
      isChangeData={!!injectionMetadata}
      metadata={finalData.metadata}
      locale={locale}
      isSelected={isSelected && isFocused}
      width={containerWidthInPC}
      jumpToCalendar={goToCalendar}
      openProfile={openProfile}
      jumpToAttachment={jumpToAttachment}
      refresh={fireRefresh}
      isMobile={isMobile}
      handleCreateCalendar={processCreateCalendar}
      createCalendarBtn={createCalendarBtn}
    />
  );
}

export default Calendar;
